.expertCard {
    font-family: Montserrat, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 40px;
}

.name {
    margin: 10px 0;
    text-transform: uppercase;
    font-weight: bold;
}

h6 {
    margin: 5px 0;
    
}

p {
    font-size: 14px;
    line-height: 21px;
}

.card-container {
    border: solid 2px white;
    background-color: #231e39;
    border-radius: 10px;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
    color: #b3b8cd;
    padding: 10px;
    margin: 0px 0px;
    position: relative;
    width: 550px;
    min-height: 300px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-container .pro {
    color: white;
    background-color: green;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 2px;
    right: 2px;
    margin: 5px;
}
.card-container .proExpired {
    color: white;
    background-color: red;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 2px;
    right: 2px;
    margin: 5px;
}
.card-container .paye {
    color: white;
    background-color: green;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 35px;
    right: 2px;
    margin: 5px;
}

.card-container .nonPaye {
    color: white;
    background-color: red;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 35px;
    right: 2px;
    margin: 5px;
}
