body {
    background: url("./Media/karate-background-academy.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    
}
li {
    list-style: none;
}
.ExpertContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 70px 0px;
}
/* Navbar css */
.nav-container{
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 1;
}
.navbar{
    background-color: rgba(231, 229, 229, 0.2);
    margin: 0;
    padding: 0;
}
.logo{
    width: 50px;
    height: 50px;
}

.applybtn{
    height: 40px;
    width: 150px;
    background-color: red;
    border: 2px solid;
    border-color: white;
    color: white;
    border-radius: 8px;
}
/* edit profile css */
.editProfileContainer{
    margin: 10%;
    border: solid 2px gray;
    border-radius: 10px;
    background: rgba(231, 229, 229, 0.3);
    text-align: center;
    color: white;
    
}
.editProfileItems{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.editInput{    
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
    background: rgba(231, 229, 229, 0.4);;
    border: solid 1px yellow;
    color: #001f3f;
    font-weight: bold;
    width: 400px
}
.checkStyle{
    margin: 10px;
}
.labelStyle{
    display: flex;
}
.editItem{
    display: flex;
    justify-content:space-between;
    align-items:center;
    width:600px
}
.inputItemsarea{
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
    background: rgba(231, 229, 229, 0.4);;
    border: solid 1px yellow;
    color: #001f3f;
    font-weight: bold;
    width: 400px;
    border-radius: 5px;
    height: 120px;
    
}
.calenderInput{
    border-radius: 5px;
    background: rgba(231, 229, 229, 0.4);;
    border: solid 1px yellow;
    color: #001f3f;
    height: 40px;
    margin: 0px 10px;
}
/* edit profile css */


.falcon{
    font-size: 120px;
    color: white;
    font-family: 'Anton', sans-serif;
}
.ressources{
    color: white;
    font-family: 'Anton', sans-serif;
    font-size: 40px;
}
.homeButton{
    border: white solid 2px;
    background: none;
    color: white;
    margin: 10px;
    width: 200px;
    height: 50px;
    padding: 10px;
}
.homeButtonG{
    -webkit-animation: bgcolor 20s infinite;
    animation: bgcolor 10s infinite;
    -webkit-animation-direction: alternate;
    animation-direction: alternate;
    border: white solid 2px;
    color: white;
    margin: 10px;
    width: 200px;
    height: 50px;
    padding: 10px;
}
.homeButton:hover{
    background: white;
    color: black;
    font-weight: bold;
}

/* search component style */

.inputSyle{
    margin-top: 70px;
}
.inputSyle input{
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
    background: rgba(231, 229, 229, 0.4);;
    border: solid 1px white;
    color: #001f3f;
    font-weight: bold;
}

