/* grille de compétences */
table {
    table-layout: fixed;
    width: auto;
    background-image: linear-gradient(to bottom, #1f5db9, #254588, #212f59, #171b2f, #010104) 
  }
   td  { 
    width: 200px;
    padding: 10px;
}
th{
    padding: 10px;
}
table, thead, tbody,tr, th, td {
    border: solid 2px white;
}
.grille{
    margin-top:80px; 
    color:white;
    ;
}

/* grille de compétences */

@keyframes bgcolor {
    0% {
        background-color: #45a3e5
    }

    30% {
        background-color: #66bf39
    }

    60% {
        background-color: #eb670f
    }

    90% {
        background-color: #f35
    }

    100% {
        background-color: #864cbf
    }
}
